import React from "react";
import Link from "gatsby-link";

import styles from './cjeline.module.scss';



export default ({ data }) => {

const LekcijeSet = new Set();

console.log(data.allMarkdownRemark.edges);


  return (
    <div className="container">
     <h2 className="title"> {data.allMarkdownRemark.edges[0].node.frontmatter.cjelina}</h2>
     <div className="breadcrumbs"><span><Link to="/">Naslovna</Link> </span><span>/ <Link to={`/${data.allMarkdownRemark.edges[0].node.frontmatter.razred}/`}>Matematika {data.allMarkdownRemark.edges[0].node.frontmatter.razred}</Link> </span><span>/ {data.allMarkdownRemark.edges[0].node.frontmatter.cjelina}</span></div>
      <div className="list">
      
      {data.allMarkdownRemark.edges.map(({ node,index }) =>
        
          <Link to={`/${node.frontmatter.razred}/${node.frontmatter.cjelina}/`} className="item"  key={node.id}>       
              {node.frontmatter.cjelina}
          </Link>
      )}
      </div>
    </div>
  )
}
export const query = graphql`
query CjelinaIndexQuery($cjelina: String) {
  allMarkdownRemark(sort: {order: ASC, fields: [frontmatter___title]}, filter: {frontmatter: {cjelina: {eq: $cjelina}}}) {
    edges {
      node {
        id
        frontmatter {
          razred
          cjelina
          lekcija
          brojCjeline
        }
      }
    }
  }
}
`


